import { keyframes, styled } from '@lokalise/styled';

import { value } from '../../utils/theme';

const Rotate360 = keyframes`
    from {
        transform: translateX(-50%) translateY(-50%) rotate(0deg);
    }
    to {
        transform: translateX(-50%) translateY(-50%) rotate(360deg);
    }
`;

export const Container = styled.div`
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: ${value('zIndex.loadingOverlay')};
`;

export const Overlay = styled.div`
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: ${value('color.background.surface.default')};
	z-index: ${value('zIndex.loadingOverlay')};
	opacity: 0.8;
`;

export const Spinner = styled.div`
	position: absolute;
	top: 50%;
	left: 50%;
	width: 20px;
	height: 20px;
	border: 3px solid ${value('color.background.surface.fore')};
	border-bottom-color: ${value('color.background.action.primary.default')};
	border-radius: 50%;
	animation: ${Rotate360} 1s linear infinite;
	z-index: inherit;
`;
